      random_color() {
        const all_color = [
          'pink lighten-2', 'red lighten-3',
          'purple lighten-1', 'deep-purple darken-2','purple accent-4',
          'indigo lighten-2', 'indigo darken-1','indigo accent-2',
          'blue lighten-2','light-blue lighten-2',
          'cyan lighten-2','cyan accent-3',
          'teal lighten-3','teal accent-2','green accent-2','green lighten-1',
          'light-green lighten-3','lime lighten-1',
          'yellow lighten-3','amber lighten-1','orange accent-3','deep-orange accent-1',
          'brown lighten-2','blue-grey lighten-1','blue-grey darken-2',
          'grey darken-2'
        ]

        const all_color_len = all_color.length
        const rnum = Math.ceil(Math.random()*all_color_len);
        return all_color[rnum]

      }
    }
  }
